import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/one",
    name: "one",
    // route level code-splitting
    // this generates a separate chunk (one.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "one" */ "../views/One.vue")
  },
  {
    path: "/two",
    name: "two",
    // route level code-splitting
    // this generates a separate chunk (two.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "two" */ "../views/Two.vue")
  }
];

const router = new VueRouter({
  routes
});

export default router;
